
.logo{
    cursor: pointer;
    text-align: center!important;
    width: 160px;
    margin-top: 30px;
    margin-bottom: none;
    border-radius: 0.25rem;
    margin-left: auto;
    margin-right: auto;
    display: block;
}

.form-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    padding-top: 50px;
  }
  
  .auth-form {
    width: 420px;
    box-shadow: rgb(0 0 0 / 16%) 1px 1px 10px;
    padding-top: 10px;
    padding-bottom: 20px;
    border-radius: 8px;
    background-color: white;
  }
  
  .auth-form-content {
    padding-left: 12%;
    padding-right: 12%;
  }
  
  .auth-form-title {
    text-align: center;
    margin-bottom: 1em;
    font-family: "Amazon Ember",Arial,sans-serif;
    font-size: 24px;
    color: rgb(34, 34, 34);
    /* font-weight: 800; */
    font-weight: 400;
    font-size: 28px;
    line-height: 1.2;
    text-rendering: optimizeLegibility;
  }
  
  label {
    font-size: 14px;
    font-weight: 600;
    color: rgb(34, 34, 34);
  }
  
  .form-group {
    margin-bottom:1rem
  }

  .mt-3 {
    margin-top: 1rem; 
  }

  .form-control {
    display: block;
    width: 90%;
    padding: 0.375rem 0.75rem;
    font-size: 1rem;
    line-height: 1.5;
    color: #495057;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: 0.25rem;
    transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  }
  
  .mt-1 {
    margin-top: 0.25rem !important;
  }
  
  .d-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 10px;
  }
 
  .gap-2 > * {
    margin-top: 2rem;
    margin-bottom: 2rem;
  }
  
  .btn {
    display: inline-block;
    font-weight: 400;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    user-select: none;
    border: 1px solid transparent;
    padding: 0.375rem 0.75rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: 0.25rem;
    transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
    border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  }
  
  /* Primary button */
  .btn-primary {
    color: #fff;
    background-color: #007bff;
    border-color: #007bff;
  }
  
  .forgot-password-btn {
    color: blue; 
    text-decoration: underline;
  }
  
  .text-right {
    text-align:right!important
  }
  
  .mb-5{
    margin-bottom:3rem!important;
  }

  .pb-lg-2{
    padding-bottom:.5rem!important;
  }